	import {skipIndex} from "../../common/js/storage.js";
	import {indexSlider} from '../../common/js/json.js';
	
    export default {
		data() {
			return {
				slides: '',
				indicatorDots: false,
				autoplay: true,
				interval: 3000,
				duration: 1000,
				circular: true,
				currentSwiper: 0,
				sliderPercent: 25
			}
		},
		methods: {
			getIndexSlider() {
				indexSlider().then(data => {
					this.slides = data;
				});
			},
			sliderChange() {
				(this.sliderPercent == 100) ? (this.sliderPercent = 25) : (this.sliderPercent += 25);
			}
		},
		onLoad() {
			skipIndex();
			this.getIndexSlider();
		}
    }
